@import "../scss/variables.scss";

.RegisterPage {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100dvh;
  min-width: 40rem;
  user-select: none;
  background: rgba(0, 0, 0, 0.468);
  backdrop-filter: blur(10px);
  padding-bottom: 10rem;
  @include respond(m) {
    padding-bottom: 20rem;
  }
  @include respond(s) {
    padding-bottom: 25rem;
  }
  & h1 {
    margin-bottom: 2rem;
  }
  & a {
    color: white;
    transition: all 0.1s;
    &:hover {
      font-weight: 700;
    }
  }
}
