.modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: max-content;
  min-width: 40vw;
  max-width: 70vw;
  height: max-content;
  margin-bottom: 2rem;
  padding: 3rem 2rem;
  & h3 {
    padding: 1rem 2rem;
    margin-bottom: 2rem;
  }
}
.modalButton {
  padding: 1.5rem 4rem;
}
