.form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 1.5rem auto;
  width: max-content;
  padding: 1rem 2rem;
  &Input {
    min-width: 60vw;
    height: 4rem;
    font-size: 2rem;
    padding: 0rem 2rem;
    border: none;
    border-radius: 15px;
    margin-right: 1rem;
    color: black;
    background-color: #ffffff96;
    &::placeholder {
      color: rgba(0, 0, 0, 0.65);
    }
    &:focus {
      background-color: #ffffff;
      outline: none;
    }
  }
  &Button {
    font-size: 3rem;
  }
}
