@import "../scss/variables.scss";

.home {
  display: flex;
  flex-direction: column;
  height: 100dvh;
  user-select: none;
  position: relative;
}

.TodoListSection {
  flex-grow: 1;
  overflow: auto;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  scroll-behavior: smooth;
  margin-bottom: 2rem;
}

.logout_button {
  background: none;
  border: none;
  text-decoration: underline;
  font-size: 1.6rem;
  width: 7rem;
  color: rgba(237, 237, 237, 0.808);
  cursor: pointer;
  transition: all 0.2s;
  font-weight: 700;
  @include hover {
    &:hover {
      color: rgb(255, 255, 255);
      transform: translateY(2px);
    }
  }
  @include touch {
    &:active {
      color: rgb(255, 255, 255);
      transform: translateY(2px);
    }
  }
}
.before_header {
  display: flex;
  justify-content: space-between;
  margin: 0.5rem 1rem 1rem 1rem;
  padding: 0.3rem 1rem 0.3rem 1rem;
}

.smile {
  display: inline-block;
}
