@import "./scss/variables.scss";

.App {
  text-align: center;
  height: 100dvh;
  user-select: none;
}

.backdropFilter {
  background: rgba(0, 0, 0, 0.468);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(54, 54, 54, 0.4);
  border-radius: 15px;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.495);
}

.mobile_horizont {
  display: none;
  @include horizont {
    height: 200px;
    width: 200px;
    background-color: rgba(0, 0, 0, 0.468);
    backdrop-filter: blur(10px);
    height: 100dvh;
    width: 100%;
    position: absolute;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  & img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    display: none;
    animation: 10s linear 0s normal none infinite running rotatePhone;

    @include horizont {
      display: block;
    }
  }
}
