@import "../scss/variables.scss";

.loginform {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
  font-family: inherit;
  @include respond(m) {
    font-size: 112.5%;
  }
  & p {
    color: rgb(248, 13, 13);
    transform: translateY(-1rem);
  }
  & input {
    font-family: inherit;
    width: 40vw;
    min-width: 35rem;
    max-width: 45rem;
    height: 4rem;
    font-size: 1.6rem;
    padding: 0rem 2rem;
    border: none;
    border-radius: 15px;
    color: black;
    background-color: #ffffff96;
    margin-bottom: 2rem;
    @include respond(m) {
      font-size: 112.5%;
      min-width: 60vw;
      height: 5rem;
    }
    @include respond(s) {
      min-width: 80vw;
      height: 6rem;
    }

    &::placeholder {
      color: rgba(0, 0, 0, 0.65);
    }
    &:focus {
      background-color: #ffffff;
      outline: none;
    }
  }
}

.enterFormButton {
  font-size: 1.8rem;
  @include respond(m) {
    font-size: 112.5%;
  }
}
