@import "../index.scss";

.todo {
  display: flex;
  align-items: center;
  padding: 1rem 1rem;
  margin: 0 auto 2.3rem auto;
  max-width: 90vw;
  border-radius: 1.5rem;
  color: #000000;
  background-color: #ffffff;
  user-select: none;
  overflow: hidden;
  min-width: min-content;
  @include respond(m) {
    flex-direction: column;
    gap: 1rem;
  }
  &Icon {
    display: block;
    font-size: 2.5rem;
    color: rgb(47, 47, 47);
    transition: all 0.3s;
    cursor: pointer;
    @include hover {
      &:hover {
        transform: scale(1.3);
        color: rgb(255, 255, 255);
      }
    }
    @include touch {
      &:active {
        transform: scale(1.3);
        color: rgb(255, 255, 255);
      }
    }
  }
  &IconGroup {
    margin: 0 1rem;
    display: flex;
    gap: 1rem;
    @include respond(m) {
      gap: 2rem;
    }
  }
  &Text {
    flex-grow: 1;
    text-align: left;
    font-size: 1.8rem;
    word-break: break-all;
    background-color: rgb(225, 225, 225);
    border-radius: 1.1rem;
    padding: 0.7rem 1rem;
    font-weight: 700;
    @include respond(m) {
      width: 100%;
    }
    &Completed {
      background-color: rgb(128, 128, 128);
      color: rgb(65, 65, 65);
    }
  }
  &Date {
    background-color: rgb(225, 225, 225);
    border-radius: 1.1rem;
    width: 9rem;
    text-align: center;
    font-size: 1.5rem;
    padding: 0.6rem 0.5rem;
    font-weight: 700;
    &Missed {
      background-color: red;
      &Completed {
        background-color: rgb(128, 128, 128);
        color: rgb(65, 65, 65);
      }
    }
  }
  &MobileGroup {
    display: flex;
    @include respond(m) {
      width: 100%;
      justify-content: space-between;
    }
  }
}
